/* eslint-disable no-unused-vars */
import vue from 'vue'
import { bus } from '../main';
/**
 * Home
 */
import HomeWelcome from './Main/Home/HomeWelcome.vue'

/**
 * Client
 */
import ClientIntro from './Main/Client/Intro/ClientIntro.vue'

import ClientDocs from './Main/Client/Docs/ClientDocs.vue'
import ClientConfig from './Main/Client/Docs/ClientConfig.vue'
import ClientLogin from './Main/Client/Docs/ClientLogin.vue'
import ClientGuide from './Main/Client/Docs/ClientGuide.vue'
import ClientNav from './Main/Client/Docs/ClientNav.vue'
import ClientPersonal from './Main/Client/Docs/ClientPersonal.vue'
import ClientProject from './Main/Client/Docs/ClientProject.vue'
import ClientSidebar from './Main/Client/Docs/ClientSidebar.vue'

import ClientAPI from './Main/Client/API/ClientAPI.vue'
import ClientAPIBase from './Main/Client/API/ClientAPIBase.vue'
import ClientAPIComponent from './Main/Client/API/ClientAPIComponent.vue'
import ClientAPIFile from './Main/Client/API/ClientAPIFile.vue'
import ClientAPIPage from './Main/Client/API/ClientAPIPage.vue'
import ClientAPIXlsx from './Main/Client/API/ClientAPIXlsx.vue'

/**
 * Server
 */
import ServerIntro from './Main/Server/Intro/ServerIntro.vue'
import ServerSetup from './Main/Server/Intro/ServerSetup.vue'
import ServerDocs from './Main/Server/Docs/ServerDocs.vue'
import ServerDocsCommand from './Main/Server/Docs/ServerDocsCommand.vue'
import ServerDocsDatabase from './Main/Server/Docs/ServerDocsDatabase.vue'
import ServerDocsNetwork from './Main/Server/Docs/ServerDocsNetwork.vue'
import ServerBackup from './Main/Server/Docs/ServerBackup.vue'

/**
 * Core
 */
import CoreIntro from './Main/Core/Intro/CoreIntro.vue'
import CoreAPI from './Main/Core/API/CoreAPI.vue'
import CoreAPIDCore from './Main/Core/API/CoreAPIDCore.vue'
import CoreAPIDElement from './Main/Core/API/CoreAPIDElement.vue'
import CoreAPIDChannel from './Main/Core/API/CoreAPIDChannel.vue'

export default vue.extend({
  name: 'Main',
  components:{
    HomeWelcome,

    ClientIntro,
    ClientDocs,
    ClientConfig,
    ClientLogin,
    ClientGuide,
    ClientNav,
    ClientPersonal,
    ClientProject,
    ClientSidebar,

    ClientAPI,
    ClientAPIBase,
    ClientAPIComponent,
    ClientAPIFile,
    ClientAPIPage,
    ClientAPIXlsx,

    ServerIntro,
    ServerSetup,
    ServerDocs,
    ServerDocsCommand,
    ServerDocsDatabase,
    ServerDocsNetwork,
    ServerBackup,
    
    CoreIntro,
    CoreAPI,
    CoreAPIDCore,
    CoreAPIDElement,
    CoreAPIDChannel,
  },
  props:{
    lan: String
  },
  created(){
    bus.$on("ChangePage", this.Page);
  },
  destroyed(){
    bus.$off("ChangePage", this.Page);
  },
  methods: {
    Page(v, index){
      if(this.page != v)
        this.page = v;
      setTimeout(() => {
        const yOffset = -10; 
        const element = document.getElementById(index);
        const y = element.getBoundingClientRect().top + window.pageYOffset + yOffset;

        window.scrollTo({top: y, behavior: 'smooth'});
      }, 20);
    }
  },
  data(){
    return {
      page: "Home-Welcome"
    }
  }
});