        </p>

        <h4 id="Command">指令系統</h4>
        <div>
            <p>
                伺服器的指令模塊提供 CLI 環境 <br>
                提供網路管理員能輸入指令與伺服器互動
            </p>
            <b-link @click="ToPage('Server-Docs-Command')">指令系統</b-link>
        </div>
        <h4 id="Network">網路組件</h4>
        <div>
            <p>
                網路模組負責解決伺服器與客戶端之間的傳輸資料動作 <br>
                伺服器的核心邏輯都在接收區的模塊
            </p>
            <b-link @click="ToPage('Server-Docs-Network')">網路組件</b-link>
        </div>
        <h4 id="Database">資料庫端口</h4>
        <div>
            <p>
                資料庫的模組提供 CRUD 動作 (建立, 讀取, 更新, 刪除) <br>
                在此之上提供對於特定資料庫的動作
            </p>
            <b-link @click="ToPage('Server-Docs-Database')">資料庫端口</b-link>
        </div>
        <h4 id="Login">登入管理員帳號</h4>
        <div>
            <p>
                在伺服器建立的時候, 後台會偵測目前資料庫內是否有帳號, 職務, 組織架構存在 <br>
                如果任意元件不存在會新增一個 <br>
                在新增帳號時, 一定會是生成管理員帳號, 並套用管理員使用的職務跟組織架構
            </p>
            <p>以下為自動生成的管理員帳號</p>
            <p>用戶名: <span class="bg-light text-primary">root</span></p>
