            </b-alert>
        </div>
    </div>
</template>

<script>
import vue from 'vue'
import { bus } from '../../../../main'
export default vue.extend({
    name: "ServerDocs",
    props:{
        lan: String
    },
    methods:{
        ToSetup(){
            bus.$emit("ChangeTopic", "Server-Intro");
            setTimeout(() => {
                bus.$emit("ChangePage", "Server-Setup");
            }, 20);
        },
        ToPage(v){
            bus.$emit("ChangePage", v)
        }
    }
})
