<template>
    <div v-if="lan=='TW'">
        <h2>客戶端 API Base</h2>
        <h4 id="BaseComponent">基礎元件</h4>
        <div>
            <p>名稱: BaseComponent</p>
            <b-button v-clipboard:copy="copyBaseComponent" @click="Alert('Copy: ' + copyBaseComponent)">Copy Import</b-button>
            <p>敘述</p>
            <p>
                套用於所有 Xanthus 元件, 內附有些方便的函式
            </p>
            <p>函式</p>
            <b-table striped hover :items="baseComponentFunction"/>
        </div>
        <h4 id="Search">搜尋欄</h4>
        <div>
            <p>名稱: SearchBar</p>
            <b-button v-clipboard:copy="copySearchBar" @click="Alert('Copy: ' + copySearchBar)">Copy Import</b-button>
            <p>屬性</p>
            <b-table striped hover :items="searchBarProp"/>
            <p>事件</p>
            <b-table striped hover :items="searchBarEvent"/>
        </div>
        <h4 id="UserAvatar">用戶頭像</h4>
        <div>
            <p>名稱: UserAvatar</p>
            <b-button v-clipboard:copy="copyUserAvatar" @click="Alert('Copy: ' + copyUserAvatar)">Copy Import</b-button>
            <p>屬性</p>
            <b-table striped hover :items="userAvatarProp"/>
            <p>事件</p>
            <b-table striped hover :items="userAvatarEvent"/>
        </div>
        <h4 id="TopButton">按鈕</h4>
        <div>
            <p>名稱: TopButton</p>
            <b-button v-clipboard:copy="copyTopButton" @click="Alert('Copy: ' + copyTopButton)">Copy Import</b-button>
            <p>屬性</p>
            <b-table striped hover :items="topButtonProp"/>
            <p>事件</p>
            <b-table striped hover :items="topButtonEvent"/>
        </div>
        <h4 id="TopFilter">過濾器</h4>
        <div>
            <p>名稱: TopFilter</p>
            <b-button v-clipboard:copy="copyTopFilter" @click="Alert('Copy: ' + copyTopFilter)">Copy Import</b-button>
            <p>屬性</p>
            <b-table striped hover :items="topFilterProp"/>
            <p>事件</p>
            <b-table striped hover :items="topFilterEvent"/>
        </div>
        <h4 id="TopDropDown">下拉式選單</h4>
        <div>
            <p>名稱: TopDropdown</p>
            <b-button v-clipboard:copy="copyTopDropdown" @click="Alert('Copy: ' + copyTopDropdown)">Copy Import</b-button>
            <p>屬性</p>
            <b-table striped hover :items="topDropdownProp"/>
            <p>事件</p>
            <b-table striped hover :items="opDropdownEvent"/>
        </div>
        <h4 id="PageSelection">頁選擇</h4>
        <h4 id="DialogTemp">彈出視窗</h4>
        <h4 id="Date-Picker">日期挑選</h4>
        <h4 id="Time-Picker">時間挑選</h4>
        <h4 id="TimeSpan-Picker">時間長度挑選</h4>
        <h4 id="Role-Picker">角色挑選</h4>
        <h4 id="Staff-Picker">員工挑選</h4>
    </div>
    <div v-else>
        <h2>Client API Base</h2>
        <h4 id="BaseComponent">BaseComponent</h4>
        <div>
            <p>Name: BaseComponent</p>
            <b-button v-clipboard:copy="copyBaseComponent" @click="Alert('Copy: ' + copyBaseComponent)">Copy Import</b-button>
            <p>Description</p>
            <p>
                Apply all Xanthus component, Have some utility function
            </p>
            <p>Function</p>
            <b-table striped hover :items="baseComponentFunction"/>
        </div>
        <h4 id="Search">搜尋欄</h4>
        <div>
            <p>Name: SearchBar</p>
            <b-button v-clipboard:copy="copySearchBar" @click="Alert('Copy: ' + copySearchBar)">Copy Import</b-button>
            <p>Prop</p>
            <b-table striped hover :items="searchBarProp"/>
            <p>Event</p>
            <b-table striped hover :items="searchBarEvent"/>
        </div>
        <h4 id="UserAvatar">用戶頭像</h4>
        <div>
            <p>Name: UserAvatar</p>
            <b-button v-clipboard:copy="copyUserAvatar" @click="Alert('Copy: ' + copyUserAvatar)">Copy Import</b-button>
            <p>Prop</p>
            <b-table striped hover :items="userAvatarProp"/>
            <p>Event</p>
            <b-table striped hover :items="userAvatarEvent"/>
        </div>
        <h4 id="TopButton">按鈕</h4>
        <div>
            <p>Name: TopButton</p>
            <b-button v-clipboard:copy="copyTopButton" @click="Alert('Copy: ' + copyTopButton)">Copy Import</b-button>
            <p>Prop</p>
            <b-table striped hover :items="topButtonProp"/>
            <p>Event</p>
            <b-table striped hover :items="topButtonEvent"/>
        </div>
        <h4 id="TopFilter">過濾器</h4>
        <div>
            <p>Name: TopFilter</p>
            <b-button v-clipboard:copy="copyTopFilter" @click="Alert('Copy: ' + copyTopFilter)">Copy Import</b-button>
            <p>Prop</p>
            <b-table striped hover :items="topFilterProp"/>
            <p>Event</p>
            <b-table striped hover :items="topFilterEvent"/>
        </div>
        <h4 id="TopDropDown">下拉式選單</h4>
        <div>
            <p>Name: TopDropdown</p>
            <b-button v-clipboard:copy="copyTopDropdown" @click="Alert('Copy: ' + copyTopDropdown)">Copy Import</b-button>
            <p>Prop</p>
            <b-table striped hover :items="topDropdownProp"/>
            <p>Event</p>
            <b-table striped hover :items="opDropdownEvent"/>
        </div>
        <h4 id="PageSelection">PageSelection</h4>
        <h4 id="DialogTemp">DialogTemp</h4>
        <h4 id="Date-Picker">Date-Picker</h4>
        <h4 id="Time-Picker">Time-Picker</h4>
        <h4 id="TimeSpan-Picker">TimeSpan-Picker</h4>
        <h4 id="Role-Picker">Role-Picker</h4>
        <h4 id="Staff-Picker">Staff-Picker</h4>
    </div>
</template>

<script>
import Base from '../../../Base'
import main from '../../../../main'
export default Base.extend({
    name: "ClientAPIBase",
    props:{
        lan: String
    },
    computed:{
        main(){
            return main;
        },
        copyBaseComponent(){
            return `import ComponentBase from "@/ComponentBase"`;
        },
        copySearchBar(){
            return `import Searchbar from "@/components/general/SearchBar/SearchBar.vue"`;
        },
        copyUserAvatar(){
            return `import UserAvatar from "@/components/general/UserAvatar/UserAvatar.vue"`;
        },
        copyTopButton(){
            return `import TopButton from "@/components/general/TopButton.vue"`;
        },
        copyTopFilter(){
            return `import TopFilter from "@/components/general/TopFilter.vue"`;
        },
        copyTopDropdown(){
            return `import TopDropdown from "@/components/general/TopDropdown.vue"`;
        },

        baseComponentFunction(){
            if(this.lan === "TW"){
                return [
                    { 名稱: "GetLanguageTags", 敘述: "從伺服器索取目前所有可用語言的標籤" },
                    { 名稱: "GetLanguageList", 敘述: "從伺服器獲得語言資料, 根據給予的標籤" },
                    { 名稱: "GetLanguage", 敘述: "從本地語言資料中搜尋字串, 根據給予的 Key" },
                    { 名稱: "GetDate", 敘述: "日期時間轉換成字串 => YYYY-MM-DD" },
                    { 名稱: "GetPreciseDate", 敘述: "日期時間轉換成字串 => YYYY-MM-DD:hh:mm:ss" },
                    { 名稱: "GetTime", 敘述: "時間長度轉換成字串 => dd:hh:mm:ss" },
                    { 名稱: "GetShortTime", 敘述: "時間長度轉換成字串 => hh:mm" },
                    { 名稱: "TimeGapSmall", 敘述: "給予時, 分, 秒, 得到時間長度" },
                    { 名稱: "TimeGapBig", 敘述: "給予年, 月, 日, 得到時間長度" },
                    { 名稱: "OpenURL", 敘述: "打開連結" },
                ]
            }else{
                return [
                    { Name: "GetLanguageTags", Description: "Get language tags from server" },
                    { Name: "GetLanguageList", Description: "Get language data from server according to input tag" },
                    { Name: "GetLanguage", Description: "Search language value by key" },
                    { Name: "GetDate", Description: "Date time to string => YYYY-MM-DD" },
                    { Name: "GetPreciseDate", Description: "Date time to string => YYYY-MM-DD:hh:mm:ss" },
                    { Name: "GetTime", Description: "Time duration to string => dd:hh:mm:ss" },
                    { Name: "GetShortTime", Description: "Time duration to string => hh:mm" },
                    { Name: "TimeGapSmall", Description: "Hour, Minute, Second to time duration" },
                    { Name: "TimeGapBig", Description: "Year, Month, Day to time duration" },
                    { Name: "OpenURL", Description: "Open URL" },
                ]
            }
        },
        searchBarProp(){
            if(this.lan === "TW"){
                return [
                    {名稱: "value", 資料型態: "String", 敘述: "搜尋的字串"},
                    {名稱: "usebutton", 資料型態: "Boolean", 敘述: "是否在右邊出現按鈕, 更新事件綁定在按鈕上"},
                ];
            }else{
                return [
                    {Name: "value", Type: "String", Description: "Search keyword"},
                    {Name: "usebutton", Type: "Boolean", Description: "Display button on the right and binding with update event"},
                ];
            }
        },
        searchBarEvent(){
            if(this.lan === "TW"){
                return [
                    {名稱: "search", 引數: "String", 敘述: "針對引數的字串進行搜尋的事件"},
                ];
            }else{
                return [
                    {名稱: "search", 引數: "String", 敘述: "Search by the text in arugment"},
                ];
            }
        },
        userAvatarProp(){
            return [];
        },
        userAvatarEvent(){
            return [];
        },
        topButtonProp(){
            return [];
        },
        topButtonEvent(){
            return [];
        },
        topFilterProp(){
            return [];
        },
        topFilterEvent(){
            return [];
        },
        topDropdownProp(){
            return [];
        },
        topDropdownEvent(){
            return [];
        },
    },
})
</script>