import vue from 'vue'
import { bus } from '../main';

import Home from './Sidebar/Home.vue'
import ClientIntro from './Sidebar/ClientIntro.vue'
import ClientDocs from './Sidebar/ClientDocs.vue'
import ClientAPI from './Sidebar/ClientAPI.vue'
import ServerIntro from './Sidebar/ServerIntro.vue'
import ServerDocs from './Sidebar/ServerDocs.vue'
import ServerAPI from './Sidebar/ServerAPI.vue'
import CoreIntro from './Sidebar/CoreIntro.vue'
import CoreAPI from './Sidebar/CoreAPI.vue'

export default vue.extend({
  name: 'Sidebar',
  components:{
    Home,
    ClientIntro,
    ClientDocs,
    ClientAPI,
    ServerIntro,
    ServerDocs,
    ServerAPI,
    CoreIntro,
    CoreAPI
  },
  props:{
    lan: String
  },
  created(){
    bus.$on("ChangeTopic", this.CTopic);
    bus.$on("ChangePage", this.CPage);
  },
  destroyed(){
    bus.$off("ChangeTopic", this.CTopic);
    bus.$off("ChangePage", this.CPage);
  },
  methods: {
    CTopic(v){
      this.topic = v;
    },
    CPage(){
      this.model = false;
    }
  },
  data(){
    return {
      model: false,
      topic: "Home"
    }
  }
})