<template>
    <div v-if="lan=='TW'">
        <h2>伺服器 控制台 文件</h2>

        <h4 id="Welcome">簡介</h4>
        <div>
            <p>
                提供網路管理員與伺服器互動的方式 <br>
                直接打入指令模塊的名稱, 會顯示所有在這個模塊下的指令
            </p>
        </div>
        <h4 id="Instruction">使用指南</h4>
        <div>
            <p>符號的辨識</p>
            <p class="bg-light text-danger m-0">[Something]</p>
            <div class="mb-4">
                引數的名稱
            </div>

            <p class="bg-light text-danger m-0">[Something(Type)]</p>
            <div class="mb-4">
                引數的名稱, 但是有資料的標註 <br>
                如: [A(Number)] [B(String)] [C(Boolean)]
            </div>

            <p class="bg-light text-danger m-0">?Something</p>
            <div class="mb-4">
                引數為選擇性的, 使用者可以選擇不用輸入 <br>
                通常這個符號會在最後面的引數
            </div>

            <p class="bg-light text-danger m-0">/</p>
            <div class="mb-4">
                <p>
                    分歧引數 <br>
                    多種文字來象徵這個引數的位置, 不影響功能, 只是方便管理員輸入指令的設計
                </p>
            </div>

            <p class="bg-light text-danger m-0">{JSON}</p>
            <div>
                <p>
                    輸入 Json 文字串 <br>
                    通常這個符號會在最後面的引數
                </p>
            </div>
        </div>


        <h4 id="Help">Help</h4>
        <div>
            <p>模塊名稱: help</p>
            <b-table striped hover :items="help"></b-table>
        </div>
        <h4 id="Account">Account</h4>
        <div>
            <p>模塊名稱: acc</p>
            <b-table striped hover :items="account"></b-table>
        </div>
        <h4 id="Class">Class</h4>
        <div>
            <p>模塊名稱: class</p>
            <b-table striped hover :items="cclass"></b-table>
        </div>
        <h4 id="Group">Group</h4>
        <div>
            <p>模塊名稱: group</p>
            <b-table striped hover :items="group"></b-table>
        </div>
        <h4 id="Post">Post</h4>
        <div>
            <p>模塊名稱: post</p>
            <b-table striped hover :items="post"></b-table>
        </div>
        <h4 id="Project">Project</h4>
        <div>
            <p>模塊名稱: pro</p>
            <b-table striped hover :items="project"></b-table>
        </div>
        <h4 id="Role">Role</h4>
        <div>
            <p>模塊名稱: role</p>
            <b-table striped hover :items="role"></b-table>
        </div>
        <h4 id="Server">Server</h4>
        <div>
            <p>模塊名稱: server</p>
            <b-table striped hover :items="server"></b-table>
        </div>
    </div>
    <div v-else>
        <h2>Server Docs Command</h2>
        <h4 id="Welcome">Intro</h4>
        <div>
            <p>
                Provide admin a way to interact with server <br>
                Enter the module name will display all command under this command module
            </p>
        </div>
        <h4 id="Instruction">Instruction</h4>
        <div>
            <p>Syntax</p>
            <p class="bg-light text-danger m-0">[Something]</p>
            <div class="mb-4">
                Argument name
            </div>

            <p class="bg-light text-danger m-0">[Something(Type)]</p>
            <div class="mb-4">
                Argument name, But with data type <br>
                Example: [A(Number)] [B(String)] [C(Boolean)]
            </div>

            <p class="bg-light text-danger m-0">?Something</p>
            <div class="mb-4">
                Arugment is optional, User can choose not to enter <br>
                This syntax always show at the last argument
            </div>

            <p class="bg-light text-danger m-0">/</p>
            <div class="mb-4">
                <p>
                    Diverse Arugment <br>
                    Multiple arugment name to repersent the position, it won't effect functionality, just make admin life easier
                </p>
            </div>

            <p class="bg-light text-danger m-0">{JSON}</p>
            <div>
                <p>
                    Json format <br>
                    This syntax always show at the last argument
                </p>
            </div>
        </div>
        <h4 id="Help">Help</h4>
        <div>
            <p>Module Name: help</p>
            <b-table striped hover :items="help"></b-table>
        </div>
        <h4 id="Account">Account</h4>
        <div>
            <p>Module Name: acc</p>
            <b-table striped hover :items="account"></b-table>
        </div>
        <h4 id="Class">Class</h4>
        <div>
            <p>Module Name: class</p>
            <b-table striped hover :items="cclass"></b-table>
        </div>
        <h4 id="Group">Group</h4>
        <div>
            <p>Module Name: group</p>
            <b-table striped hover :items="group"></b-table>
        </div>
        <h4 id="Post">Post</h4>
        <div>
            <p>Module Name: post</p>
            <b-table striped hover :items="post"></b-table>
        </div>
        <h4 id="Project">Project</h4>
        <div>
            <p>Module Name: pro</p>
            <b-table striped hover :items="project"></b-table>
        </div>
        <h4 id="Role">Role</h4>
        <div>
            <p>Module Name: role</p>
            <b-table striped hover :items="role"></b-table>
        </div>
        <h4 id="Server">Server</h4>
        <div>
            <p>Module Name: server</p>
            <b-table striped hover :items="server"></b-table>
        </div>
    </div>
</template>

<script>
import vue from 'vue'
export default vue.extend({
    name: "ServerDocsNetwork",
    props:{
        lan: String
    },
    computed:{
        help(){
            if(this.lan === "TW"){
                return [
                    {引數: "cm", 敘述: "列出所有指令模組"},
                    {引數: "hk", 敘述: "顯示控制台快敏按鍵"},
                ];
            }else{
                return [
                    {Argument: "cm", description: "Display all command modules"},
                    {Argument: "hk", description: "Display console hotkey"},
                ];
            }
        },
        account(){
            if(this.lan === "TW"){
                return [
                    {引數: "create [Username] [Password]", 敘述: "建立帳號, 如果用戶名已存在, 建立將會失敗"},
                    {引數: "ban [Reason]", 敘述: "封鎖帳號"},
                    {引數: "pardon", 敘述: "解除封鎖"},
                    {引數: "kick [Reason]", 敘述: "強制登出"},
                    {引數: "join [ProjectID]", 敘述: "加入專案"},
                    {引數: "leave [ProjectID]", 敘述: "從專案去除掉"},
                    {引數: "modify {JSON}", 敘述: "修改帳號詳細"},
                ];
            }else{
                return [
                    {Argument: "create [Username] [Password]", description: "Create account, If username already exist, the creation will failed"},
                    {Argument: "ban [Reason]", description: "Ban account"},
                    {Argument: "pardon", description: "Pardon account"},
                    {Argument: "kick [Reason]", description: "Force logout"},
                    {Argument: "join [ProjectID]", description: "Join project"},
                    {Argument: "leave [ProjectID]", description: "Force remove from project"},
                    {Argument: "modify {JSON}", description: "Modify account information"},
                ];
            }
        },
        cclass(){
            if(this.lan === "TW"){
                return [
                    {引數: "create [name]", 敘述: "建立新的職務"},
                    {引數: "modify {JSON}", 敘述: "修改職務"},
                ];
            }else{
                return [
                    {Argument: "create [name]", description: "Create an new class"},
                    {Argument: "modify {JSON}", description: "Modify class by json data"},
                ];
            }
        },
        group(){
            if(this.lan === "TW"){
                return [];
            }else{
                return [];
            }
        },
        post(){
            if(this.lan === "TW"){
                return [];
            }else{
                return [];
            }
        },
        project(){
            if(this.lan === "TW"){
                return [
                    {引數: "create [name] [budget(Number)] [owner] [?group]", 敘述: "建立專案, Owner 必要要是合法的帳號 ID, 引數 Group 代表指定加入專案群組"},
                    {引數: "move/mv [group]", 敘述: "移動專案到指定專案群組"},
                    {引數: "moven/mvn [groupName]", 敘述: "移動專案到新的專案群組, 引數 groupName 代表新的專案群組名稱"},
                    {引數: "owner [Account]", 敘述: "轉移專案所有權"},
                    {引數: "staff [?Page(Number)]", 敘述: "查看會員清單"},
                    {引數: "group [?Page(Number)]", 敘述: "查看部門清單"},
                    {引數: "role [?Page(Number)]", 敘述: "查看角色清單"},
                    {引數: "invite [?Page(Number)]", 敘述: "查看邀請碼清單"},
                ];
            }else{
                return [
                    {Argument: "create [name] [budget(Number)] [owner] [?group]", description: "Create project, Owner must be vaild account ID, Argument Group means the join project group"},
                    {Argument: "move/mv [group]", description: "Moving project to select project group"},
                    {Argument: "moven/mvn [groupName]", description: "Moving project to new project group, Arugment groupName means the new project group name"},
                    {Argument: "owner [Account]", description: "Change project owner"},
                    {Argument: "staff [?Page(Number)]", description: "Print staff list"},
                    {Argument: "group [?Page(Number)]", description: "Print department list"},
                    {Argument: "role [?Page(Number)]", description: "Print role list"},
                    {Argument: "invite [?Page(Number)]", description: "Print invide code list"},
                ];
            }
        },
        role(){
            if(this.lan === "TW"){
                return [];
            }else{
                return [];
            }
        },
        server(){
            if(this.lan === "TW"){
                return [
                    {引數: "clean/cc", 敘述: "清除控制台訊息"},
                    {引數: "max [line(Number)]", 敘述: "最大顯示行數 (預設: 5000)"},
                    {引數: "list [?page(Number)]", 敘述: "連線清單"},
                    {引數: "online/on [?page(Number)]", 敘述: "登入用戶清單"},
                    {引數: "log [level(Number)]", 敘述: "紀錄等級, 0 - 5, 數字高時任意微小的行為都會記錄出來, 數字低時只會顯示重要的訊息, (預設: 5)"},
                    {引數: "status", 敘述: "顯示伺服器目前狀態 (資料庫連接狀態, 伺服器啟動狀態)"},
                    {引數: "start [?port]", 敘述: "啟動伺服器, port: 連接埠"},
                    {引數: "shutdown/sd", 敘述: "關閉伺服器"},
                    {引數: "connection/conn [?address]", 敘述: "連接資料庫, address: 地址"},
                    {引數: "disconnect/disconn", 敘述: "與資料庫斷線"},
                    {引數: "reset", 敘述: "刪除資料庫, 刪除媒體資料 (危險)"},
                    {引數: "kickall", 敘述: "強制登出所有人"},
                ];
            }else{
                return [
                    {Argument: "clean/cc", description: "Clean console message"},
                    {Argument: "max [line(Number)]", description: "Maximum line display (Default: 5000)"},
                    {Argument: "list [?page(Number)]", description: "Connection list"},
                    {Argument: "online/on [?page(Number)]", description: "Login client list"},
                    {Argument: "log [level(Number)]", description: "Log level, 0 - 5, When number is high: any trivial event will print it out, When number is low: it only log important event, (default: 5)"},
                    {Argument: "status", description: "Display server current status (Database link, Http server active state)"},
                    {Argument: "start [?port]", description: "Start the server"},
                    {Argument: "shutdown/sd", description: "Shutdown the server"},
                    {Argument: "connection/conn [?address]", description: "Connect to database"},
                    {Argument: "disconnect/disconn", description: "Disconnect from database"},
                    {Argument: "reset", description: "Delete database, Delete media files (dangerous)"},
                    {Argument: "kickall", description: "Force logout everyone"},
                ];
            }
        },
    }
})
</script>