        <h4 id="Account">Account</h4>
        <p>
            Store user information
        </p>
        <b-table striped hover :items="account"></b-table>

        <h4 id="Project">Project</h4>
        <p>
            Store project information
        </p>
        <b-table striped hover :items="project"></b-table>

        <h4 id="ProjectGroup">Project Group</h4>
        <p>
            Store project group information
        </p>
        <b-table striped hover :items="projectGroup"></b-table>

        <h4 id="Channel">Channel</h4>
        <p>
            Store channel information
        </p>
        <b-table striped hover :items="channel"></b-table>
    </div>
</template>

