                    {名稱: "email", 型態: "String", 敘述: "公司聯絡使用的電子郵件"},
                    {名稱: "username", 型態: "String", 敘述: "用戶名, 登入進行使用"},
                    {名稱: "password", 型態: "String", 敘述: "密碼 (加密過)"},
                    {名稱: "identitycard", 型態: "String", 敘述: "身分證字號"},
                    {名稱: "firstname", 型態: "String", 敘述: "姓"},
                    {名稱: "lastname", 型態: "String", 敘述: "名"},
                    {名稱: "passportname", 型態: "String", 敘述: "護照名稱"},
                    {名稱: "phone", 型態: "String", 敘述: "電話聯絡"},
                    {名稱: "firstdayofwork", 型態: "Number", 敘述: "入職日"},
                    {名稱: "lastdayofwork", 型態: "Number", 敘述: "離職日"},
                    {名稱: "class", 型態: "String", 敘述: "用戶的身分 (職務 ID)"},
                    {名稱: "organ", 型態: "Array<String>", 敘述: "用戶的組織定位 (組織 ID)"},
                    {名稱: "laborinsurance", 型態: "Number", 敘述: "勞工保險花費"},
                    {名稱: "healthinsurance", 型態: "Number", 敘述: "健康保險花費"},
                    {名稱: "pension", 型態: "Number", 敘述: "退撫金"},
                    {名稱: "block", 型態: "Boolean", 敘述: "封鎖狀態"},
                    {名稱: "googlekey", 型態: "String", 敘述: "Google Docs 的 Key"},
                    {名稱: "projects", 型態: "Array<String>", 敘述: "加入的專案 (專案 ID)"},
                    {名稱: "private", 型態: "Array<String>", 敘述: "私人頻道的排序 (頻道 ID)"},
                ]
            }else{
                return [
                    {name: "level", type: "Number", description: "Class level"},
                    {name: "email", type: "String", description: "Company contact use email"},
                    {name: "username", type: "String", description: "For login use"},
                    {name: "password", type: "String", description: "Hashed password"},
                    {name: "identitycard", type: "String", description: "ID card number"},
                    {name: "firstname", type: "String", description: "First name"},
                    {name: "lastname", type: "String", description: "Last name"},
                    {name: "passportname", type: "String", description: "Passport name"},
                    {name: "phone", type: "String", description: "Company contact use cellphone number"},
                    {name: "firstdayofwork", type: "Number", description: "Join day"},
