                return [
                    {name: "name", type: "String", description: "Display name"},
                    {name: "secondary", type: "String", description: "Secondary name"},
                    {name: "belong", type: "String", description: "Belong which project group (Project Group ID)"},
                    {name: "description", type: "String", description: "Project description"},
                    {name: "budget", type: "Number", description: "Will need this data when calculate spending "},
                    {name: "startday", type: "Number", description: "Project start day"},
                    {name: "endday", type: "Number", description: "Project end day"},
