                    {name: "projects", type: "Array<String>", description: "Project list (Project ID)"},
                ]
            }
        },
        channel(){
            if(this.lan == 'TW'){
                return [
                    {名稱: "type", 型態: "Number", 敘述: "頻道型態"},
                    {名稱: "groupid", 型態: "String", 敘述: "部門的子頻道註冊 (部門 ID)"},
                    {名稱: "textSetting", 型態: "TextSetting", 敘述: "文字頻道設定"},
                    {名稱: "fileSetting", 型態: "FileSetting", 敘述: "檔案頻道設定"},
                    {名稱: "issueSetting", 型態: "IssueSetting", 敘述: "論壇頻道設定"},
                    {名稱: "requestSetting", 型態: "RequestSetting", 敘述: "審查頻道設定"},
                    {名稱: "billSetting", 型態: "BillSetting", 敘述: "帳單頻道設定"},
                    {名稱: "graphSetting", 型態: "GraphSetting", 敘述: "圖表頻道設定"},
                    {名稱: "outsourceSetting", 型態: "OutsourceSetting", 敘述: "外包頻道設定"},
                    {名稱: "taskSetting", 型態: "TaskSetting", 敘述: "任務頻道設定"},
                    {名稱: "PMChannel", 型態: "PMChannel", 敘述: "私人頻道資料"},
                ]
