                return [
                    {name: "type", type: "Number", description: "頻道type"},
                    {name: "groupid", type: "String", description: "部門的子頻道註冊 (部門 ID)"},
                    {name: "textSetting", type: "TextSetting", description: "文字頻道設定"},
                    {name: "fileSetting", type: "FileSetting", description: "檔案頻道設定"},
                    {name: "issueSetting", type: "IssueSetting", description: "論壇頻道設定"},
                    {name: "requestSetting", type: "RequestSetting", description: "審查頻道設定"},
                    {name: "billSetting", type: "BillSetting", description: "帳單頻道設定"},
                    {name: "graphSetting", type: "GraphSetting", description: "圖表頻道設定"},
                    {name: "outsourceSetting", type: "OutsourceSetting", description: "外包頻道設定"},
                    {name: "taskSetting", type: "TaskSetting", description: "任務頻道設定"},
                    {name: "PMChannel", type: "PMChannel", description: "私人頻道資料"},
                ]
            }
        }
    }
})
</script>