<template>
    <div v-if="lan=='TW'">
        <h2>客戶端 Xlsx API </h2>
        <h4 id="Component">Xlsx 元件</h4>
        <div>
            <p>名稱: ImportDataset</p>
            <b-button v-clipboard:copy="copyComponent" @click="Alert('Copy: ' + copyComponent)">Copy Import</b-button>
            <p>敘述</p>
            <p>
                彈出性視窗, 匯入 Xlsx 與查看資料的地方 <br>
                透過給予函數指標, 動態的定義解釋器 <br>
                這個元件就能解決各式各樣的格式 <br>
                <br>
            </p>
            <p>元件</p>
            <ul>
                <li>DialogTemp</li>
            </ul>
            <p>屬性</p>
            <b-table striped hover :items="componentProp"/>
            <p>事件</p>
            <b-table striped hover :items="componentEvent"/>
        </div>
        <h4 id="TaskContainer">解釋器:任務群組</h4>
        <div>
            <p>名稱: ImportTaskContainerData</p>
            <b-link target="_blank" href="./example/ExampleTaskContainerDataset.xlsx">範例</b-link>
            <b-table striped hover :items="taskContainerFormat"/>
        </div>
        <h4 id="Task">解釋器:任務</h4>
        <div>
            <p>名稱: ImportTaskData</p>
            <p>
                垂直延伸的方式來支援陣列圖片與陣列指向任務群組元素
            </p>
            <b-link target="_blank" href="./example/ExampleTaskDataset.xlsx">範例</b-link>
            <b-table striped hover :items="taskFormat"/>
        </div>
        <h4 id="Class">解釋器:職務</h4>
        <div>
            <p>名稱: ImportClassData</p>
            <b-link target="_blank" href="./example/ExampleClassDataset.xlsx">範例</b-link>
            <p>
                第一行為 Header, 並不會加入至資料當中 <br>
                讀取的方式依序讀取行數 <br>
                背景計算目前指針行元素的 等級欄位 <br>
            </p>
            <p>分為兩種情況</p>
            <h5> 1. 讀取時發現等級是 1 </h5>
            <ol>
                <li>把目前緩衝區的資料加入回傳陣列</li>
                <li>清空緩衝區</li>
                <li>加入目前讀取的資料到緩衝區</li>
                <li>加入第一段 Level </li>
            </ol>
            <h5> 2. 讀取時發現等級不是 1 </h5>
            <p>
                <ol>
                    <li>迴圈緩衝區資料</li>
                    <li>加入新 Level 資料至最後一位</li>
                </ol>
            </p>
            <b-table striped hover :items="classFormat"/>
        </div>
        <h4 id="Organ">解釋器:組織</h4>
        <div>
            <p>名稱: ImportOrganizationData</p>
            <b-link target="_blank" href="./example/ExampleOrganDataset.xlsx">範例</b-link>
            <p>
                第一行為 Header, 並不會加入至資料當中 <br>
                上層的順位意指偵測的順序 <br>
                如果兩個欄位皆有值, 則這個順位才有意義 <br>
                <br>
                權限的陣列輸入方式為 => '0,2,5,7' <br>
                每個代號對應到指定的權限 <br>
                如果超出範圍會無視
            </p>
            <b-alert variant="warning" show>
                如果你想使用 上層 ID, 必須遵守順序 <br>
                你不能指定一個尚未創立的組織元素
            </b-alert>
            <b-table striped hover :items="organFormat"/>
        </div>
        <h4 id="Group">解釋器:部門</h4>
        <div>
            <p>名稱: ImportDepartmentData</p>
            <b-link target="_blank" href="./example/ExampleGroupDataset.xlsx">範例</b-link>
            <b-table striped hover :items="groupFormat"/>
        </div>
    </div>
    <div v-else>
        <h2>Client Xlsx API</h2>
        <h4 id="Component">Component</h4>
        <div>
            <p>Name: ImportDataset</p>
            <b-button v-clipboard:copy="copyComponent" @click="Alert('Copy: ' + copyComponent)">Copy Import</b-button>
            <p>Descrption</p>
            <p>
                Popup, Import Xlsx file and check data <br>
                Given decoder function pointer, dynamically convert data <br>
                This component can solve different format of spreadsheet <br>
                <br>
            </p>
            <p>Components</p>
            <ul>
                <li>DialogTemp</li>
            </ul>
            <p>Prop</p>
            <b-table striped hover :items="componentProp"/>
            <p>Event</p>
            <b-table striped hover :items="componentEvent"/>
        </div>
        <h4 id="TaskContainer">Decoder:Task Container</h4>
        <div>
            <p>Name: ImportTaskContainerData</p>
            <b-link target="_blank" href="./example/ExampleTaskContainerDataset.xlsx">Exmaple</b-link>
            <b-table striped hover :items="taskContainerFormat"/>
        </div>
        <h4 id="Task">Decoder:Task</h4>
        <div>
            <p>Name: ImportTaskData</p>
            <p>
                Vertical extend to support image array and task container target array
            </p>
            <b-link target="_blank" href="./example/ExampleTaskDataset.xlsx">Exmaple</b-link>
            <b-table striped hover :items="taskFormat"/>
        </div>
        <h4 id="Class">Decoder:Class</h4>
        <div>
            <p>Name: ImportClassData</p>
            <b-link target="_blank" href="./example/ExampleClassDataset.xlsx">Example</b-link>
            <p>
                First row is Header, it won't load into dataset <br>
                Loading data row by row <br>
                Background will counting current load level field <br>
            </p>
            <p>Two situation</p>
            <h5> 1. Loaded and see level is 1 </h5>
            <ol>
                <li>Load buffer data into result data</li>
                <li>Clean buffer</li>
                <li>Load current data into buffer</li>
                <li>Push first Level data</li>
            </ol>
            <h5> 2. Loaded and see level is not 1 </h5>
            <p>
                <ol>
                    <li>Loop buffer data</li>
                    <li>Push new Level data to last position of array</li>
                </ol>
            </p>
            <b-table striped hover :items="classFormat"/>
        </div>
        <h4 id="Organ">Decoder:Organ</h4>
        <div>
            <p>Name: ImportOrganizationData</p>
            <b-link target="_blank" href="./example/ExampleOrganDataset.xlsx">Example</b-link>
            <p>
                First row is Header, it won't load into dataset <br>
                Parent order means detect ID function order <br>
                Order is important if both field have value <br>
                <br>
                Permission array input is => '0,2,5,7' <br>
                Each code match a permission <br>
                If out of bound will ignore <br>
            </p>
            <b-alert variant="warning" show>
                If you want to use parent ID, it must follow by order <br>
                You cannot assign the ID before it create
            </b-alert>
            <b-table striped hover :items="organFormat"/>
        </div>
        <h4 id="Group">Decoder:Group</h4>
        <div>
            <p>Name: ImportDepartmentData</p>
            <b-link target="_blank" href="./example/ExampleGroupDataset.xlsx">Example</b-link>
            <b-table striped hover :items="groupFormat"/>
        </div>
    </div>
</template>

<script>
import Base from '../../../Base'
import main from '../../../../main'
export default Base.extend({
    name: "ClientAPIXlsx",
    props:{
        lan: String
    },
    computed:{
        main(){
            return main;
        },
        copyComponent(){
            return `import ImportDataset from "@/components/general/dialog/ImportDataset/ImportDataset.vue"`;
        },
        componentProp(){
            if(this.lan == "TW"){
                return [
                    {名稱: "value", 資料型態: "Boolean", 敘述: "當前彈出視窗的視窗顯示值"},
                    {名稱: "title", 資料型態: "String", 敘述: "這個視窗的標題, 預設: 'Import Dataset'"},
                    {名稱: "header", 資料型態: "String", 敘述: "哪一個欄位當作標頭 (ID 用途 和 顯示用途)"},
                    {名稱: "top", 資料型態: "Array<String>", 敘述: "物件顯示欄位, 透過 key 尋找最上層物件並且加入顯示的清單"},
                    {名稱: "sub", 資料型態: "Array<String>", 敘述: "子物件顯示欄位, 透過 key 尋找第二層物件並且加入顯示的清單"},
                    {名稱: "filter", 資料型態: "String", 敘述: "選取的解釋器名稱"},
                ]
            }else{
                return [
                    {Name: "value", Type: "Boolean", Description: "Dialog popup value"},
                    {Name: "title", Type: "String", Description: "This windows title, Default: 'Import Dataset'"},
                    {Name: "header", Type: "String", Description: "Which field to be use at title (ID and display purpose)"},
                    {Name: "top", Type: "Array<String>", Description: "First layer object display, search object by key and show on display"},
                    {Name: "sub", Type: "Array<String>", Description: "Second layer object display, search object by key and show on display"},
                    {Name: "filter", Type: "String", Description: "Select Decoder name"},
                ]
            }
        },
        componentEvent(){
            if(this.lan == "TW"){
                return [
                    {名稱: "apply", 引數: "Array<any>", 敘述: "確認匯入資料"},
                ]
            }else{
                return [
                    {Name: "apply", Arugment: "Array<any>", Description: "Confirm import data"},
                ]
            }
        },
        taskContainerFormat(){
            if(this.lan == "TW"){
                return [
                    {欄位: "B", 資料型態: "String", 名稱: "部門 ID"},
                    {欄位: "C", 資料型態: "String", 名稱: "名稱"},
                    {欄位: "D", 資料型態: "String | Null", 名稱: "內容"},
                    {欄位: "E", 資料型態: "Date", 名稱: "開始時間"},
                    {欄位: "F", 資料型態: "Number", 名稱: "人力"},
                ]
            }else{
                return [
                    {Column: "B", Type: "String", Name: "Department ID"},
                    {Column: "C", Type: "String", Name: "Name"},
                    {Column: "D", Type: "String | Null", Name: "Content"},
                    {Column: "E", Type: "Date", Name: "Start datetime"},
                    {Column: "F", Type: "Number", Name: "Man power"},
                ]
            }
        },
        taskFormat(){
            if(this.lan == "TW"){
                return [
                    {欄位: "B", 資料型態: "String", 名稱: "名稱"},
                    {欄位: "C", 資料型態: "String | Null", 名稱: "內容"},
                    {欄位: "D", 資料型態: "Image | Null", 名稱: "圖片"},
                    {欄位: "E", 資料型態: "String", 名稱: "任務群組 ID"},
                    {欄位: "F", 資料型態: "Number", 名稱: "估計工作時間"},
                ]
            }else{
                return [
                    {Column: "B", Type: "String", Name: "Name"},
                    {Column: "C", Type: "String | Null", Name: "Content"},
                    {Column: "D", Type: "Image | Null", Name: "Image"},
                    {Column: "E", Type: "String", Name: "Task container ID"},
                    {Column: "F", Type: "Number", Name: "Estimate time"},
                ]
            }
        },
        classFormat(){
            if(this.lan == "TW"){
                return [
                    {欄位: "B", 資料型態: "Number", 名稱: "技術責任職"},
                    {欄位: "C", 資料型態: "Number", 名稱: "職等"},
                    {欄位: "D", 資料型態: "Number", 名稱: "等級"},
                    {欄位: "E", 資料型態: "Number", 名稱: "薪資"},
                    {欄位: "F", 資料型態: "Number", 名稱: "薪資底限"},
                    {欄位: "G", 資料型態: "Number", 名稱: "伙食費"},
                    {欄位: "H", 資料型態: "Number", 名稱: "考績標準"},
                    {欄位: "I", 資料型態: "Number", 名稱: "職務加級"},
                    {欄位: "J", 資料型態: "Number", 名稱: "基本底薪"},
                    {欄位: "K", 資料型態: "String | Null", 名稱: "(C) 幕僚管理職"},
                    {欄位: "L", 資料型態: "String | Null", 名稱: "(C) 幕僚非管理職"},
                    {欄位: "M", 資料型態: "String | Null", 名稱: "(C) 技術管理職"},
                    {欄位: "N", 資料型態: "String | Null", 名稱: "(C) 技術非管理職"},
                    {欄位: "O", 資料型態: "String | Null", 名稱: "(C) 美術職務"},
                    {欄位: "P", 資料型態: "String | Null", 名稱: "(C) 導演職務"},
                ]
            }else{
                return [
                    {Column: "B", Type: "Number", Name: "Technical Responsibility"},
                    {Column: "C", Type: "Number", Name: "Rank"},
                    {Column: "D", Type: "Number", Name: "Level"},
                    {Column: "E", Type: "Number", Name: "Salary"},
                    {Column: "F", Type: "Number", Name: "Salary Limit"},
                    {Column: "G", Type: "Number", Name: "Food"},
                    {Column: "H", Type: "Number", Name: "performance Appraisal Standard"},
                    {Column: "I", Type: "Number", Name: "Promote"},
                    {Column: "J", Type: "Number", Name: "Basic Salary"},
                    {Column: "K", Type: "String | Null", Name: "(C) Staff Management Position"},
                    {Column: "L", Type: "String | Null", Name: "(C) Staff None Management Position"},
                    {Column: "M", Type: "String | Null", Name: "(C) Technical Management Position"},
                    {Column: "N", Type: "String | Null", Name: "(C) Technical None Management Position"},
                    {Column: "O", Type: "String | Null", Name: "(C) Artist"},
                    {Column: "P", Type: "String | Null", Name: "(C) Directorship"},
                ]
            }
        },
        organFormat(){
            if(this.lan == "TW"){
                return [
                    {欄位: "B", 資料型態: "String", 名稱: "ID"},
                    {欄位: "C", 資料型態: "String", 名稱: "名稱"},
                    {欄位: "D", 資料型態: "String | Null", 名稱: "部門"},
                    {欄位: "E", 資料型態: "String | Null", 名稱: "上層 ID (第一順位)"},
                    {欄位: "F", 資料型態: "String | Null", 名稱: "上層 UUID (第二順位)"},
                    {欄位: "G", 資料型態: "Array<Number>", 名稱: "權限"},
                ]
            }else{
                return [
                    {Column: "B", Type: "String", Name: "ID"},
                    {Column: "C", Type: "String", Name: "Name"},
                    {Column: "D", Type: "String | Null", Name: "Department"},
                    {Column: "E", Type: "String | Null", Name: "Parent ID (First order)"},
                    {Column: "F", Type: "String | Null", Name: "Parent UUID (Secord order)"},
                    {Column: "G", Type: "Array<Number>", Name: "Permission"},
                ]
            }
        },
        groupFormat(){
            if(this.lan == "TW"){
                return [
                    {欄位: "B", 資料型態: "String", 名稱: "名稱"},
                    {欄位: "C", 資料型態: "String | Null", 名稱: "敘述"},
                    {欄位: "D", 資料型態: "String | Null", 名稱: "負責人 ID"},
                    {欄位: "E", 資料型態: "String | Null", 名稱: "副負責人 ID"},
                ]
            }else{
                return [
                    {Column: "B", Type: "String", Name: "Name"},
                    {Column: "C", Type: "String | Null", Name: "Description"},
                    {Column: "D", Type: "String | Null", Name: "Charge"},
                    {Column: "E", Type: "String | Null", Name: "Duputy"},
                ]
            }
        },
    },
})
</script>