<template>
    <div v-if="lan=='TW'">
        <h2>客戶端 紀錄檔案</h2>
        <h4 id="Appearance">風格設定檔</h4>
        <div>
            <p>檔案路徑: [程式根目錄]/appearance.json</p>
            <ul> Theme
                <li>0: 白色</li>
                <li>1: 黑色</li>
            </ul>
            <b-table :items="appearance"></b-table>
        </div>
        <h4 id="Config">軟體紀錄檔</h4>
        <div>
            <p>檔案路徑: [程式根目錄]/config.json</p>
            <b-table :items="config"></b-table>
        </div>
    </div>
    <div v-else>
        <h2>Client Config</h2>
        <h4 id="Appearance">Appearance File</h4>
        <div>
            <p>File path: [Program Root]/appearance.json</p>
            <ul> Theme
                <li>0: White</li>
                <li>1: Dark</li>
            </ul>
            <b-table :items="appearance"></b-table>
        </div>
        <h4 id="Config">Config File</h4>
        <div>
            <p>File path: [Program Root]/config.json</p>
            <b-table :items="config"></b-table>
        </div>
    </div>
</template>

<script>
import vue from 'vue'
import main from '../../../../main'
export default vue.extend({
    name: "ClientDocs",
    props:{
        lan: String
    },
    computed:{
        main(){
            return main;
        },
        appearance(){
            if(this.lan == "TW"){
                return [
                    {名稱: "Theme", 敘述: "使用的風格, 預設為 0"},
                    {名稱: "Lan", 敘述: "選擇的語言標籤, 預設為 'EN'"}
                ]
            }else{
                return [
                    {Name: "Theme", Description: "Use theme, default: 0"},
                    {Name: "Lan", Description: "Use language tag, Default: 'EN'"}
                ]
            }
        },
        config(){
            if(this.lan == "TW"){
                return [
                    {名稱: "Address", 敘述: "上次連線成功的位置, 連線成功時會記錄下來, 等下次登入的時候會嘗試自動連線."}
                ]
            }else{
                return [
                    {Name: "Address", Description: "Last time connection, When connection successfully created, it will store the value and try re-connect when next time open client"}
                ]
            }
        }
    },
})
</script>